import React from 'react'
import { Layout, Menu } from 'antd'
import PageName from 'constant/PageName'
import history from 'utils/History'

const { Sider } = Layout

export interface ISideBarProps {}

export interface ISideBarState {}

const MenuList = [
  {
    content: 'Inventory',
    key: PageName.inventory.split('/')[1],
    path: PageName.inventory,
  },
  {
    content: 'Transaction',
    key: PageName.transaction.split('/')[1],
    path: PageName.transaction,
  },
  {
    content: 'Log out',
    key: PageName.login.split('/')[1],
    path: PageName.login,
  },
]

class SideBar extends React.PureComponent<ISideBarProps, ISideBarState> {
  state = { currentMenu: [] }
  renderListMenu = (props: any) => (
    <Menu.Item
      className={'mt-0 mb-0'}
      key={props.key}
      onClick={() => {
        history.replace(props.key)
        this.forceUpdate()
      }}
    >
      {props.content}
    </Menu.Item>
  )

  render() {
    // let keyPath = window.location.pathname
    let keyPath = window.location.pathname.split('/')
    const selectKey = [keyPath[2]]

    return (
      <Sider width={150} className="site-layout-background">
        <Menu
          theme="dark"
          mode="inline"
          defaultSelectedKeys={[PageName.inventory.split('/')[1]]}
          defaultOpenKeys={[PageName.inventory.split('/')[1]]}
          style={{ height: '100%', borderRight: 0 }}
          selectedKeys={selectKey}
        >
          {MenuList.map((menu) => this.renderListMenu(menu))}
        </Menu>
      </Sider>
    )
  }
}

export default SideBar
